/* New Styles for the application to match the landing page */
.chat-container {
  display: flex;
  flex-direction: column;
  height: 93vh;
  background-color: var(--color-background);
  font-family: var(--font-family);
}

.chat-header {
  padding: 20px;
  background-color: #f0f2f5;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 80%;
  margin: 0 auto;
  box-shadow: 0px 5px 15px #4458dc3a !important;
}

.chat-messages {
  flex: 1;
  overflow: auto; /* o scroll */
  scrollbar-width: none; /* Para Firefox */
  -ms-overflow-style: none; /* Para Internet Explorer y Edge */
  padding: 20px;
  display: flex;
  flex-direction: column;
  max-width: 800px;
  min-width: 800px;
  align-self: center;
  gap: 15px;
}

.chat-messages::-webkit-scrollbar {
  display: none; /* Oculta la barra en Chrome, Safari y Edge */
}

.chat-message {
  max-width: 70%;
  padding: 15px 20px;
  border-radius: var(--border-radius);
  position: relative;
  word-wrap: break-word;
  box-shadow: var(--box-shadow);
  transition: transform var(--transition-speed), box-shadow var(--transition-speed);
}

.chat-message.user {
  background-color: var(--color-primary);
  color: #ffffff;
  align-self: flex-end;
  border-bottom-right-radius: 0;
}

.chat-message.assistant {
  background-color: var(--color-secondary);
  color: var(--color-text);
  align-self: flex-start;
  border-bottom-left-radius: 0;
}

.chat-input {
  padding: 20px;
  background-color: var(--color-background);
  display: flex;
  align-items: center;
  border-top: 1px solid #ddd;
  box-shadow: none;
  border: none;
  width: 100%;
  max-width: 80%;
  margin: 0 auto;
  box-shadow: 0px 5px 15px #4458dc3a !important;
}

.chat-input input {
  flex: 1;
  padding: 14px 20px;
  border-radius: var(--border-radius);
  outline: none;
  font-size: 16px;
  transition: border-color var(--transition-speed);
}

.chat-input input:focus {
  border-color: var(--color-primary);
}

.chat-input button {
  margin-left: 15px;
  background-color: var(--color-primary);
  color: #ffffff;
  border: none;
  padding: 14px;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: background-color var(--transition-speed), transform var(--transition-speed);
  box-shadow: var(--box-shadow);
}

.chat-input button:hover {
  background-color: #3751a0; /* Darker shade of primary color */
  transform: translateY(-1px);
}

hr {
  border-color: #ffffff45;
  margin-bottom: 0;
}

/* Responsive Design */
@media (max-width: 600px) {

  .chat-input {
    max-width: 100%;
    padding: 5px;
  }
  .chat-container {
    padding: 10px;
  }

  .chat-header {
    flex-direction: column;
    align-items: flex-start;
    padding: 15px 5px;
    max-width: 100%;
  }

  .chat-messages {
    max-width: 100%;
    min-width: auto;
    padding: 15px;
  }

  .chat-input input {
    padding: 10px 14px;
    font-size: 14px;
  }

  .chat-input button {
    padding: 10px;
    margin-left: 10px;
  }

  .chat-message {
    max-width: 80%;
    padding: 12px 16px;
  }
}

/* Additional General Styles */
.MuiCard-root {
  transition: transform var(--transition-speed), box-shadow var(--transition-speed);
  border-radius: var(--border-radius);
}

.MuiCard-root:hover {
  transform: translateY(-1px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.MuiButton-root {
  text-transform: none;
  font-weight: var(--font-weight-regular);
  border-radius: var(--border-radius);
  transition: background-color var(--transition-speed), box-shadow var(--transition-speed);
}

.MuiButton-root:hover {
  box-shadow: var(--box-shadow);
}

.MuiTypography-h4 {
  font-weight: var(--font-weight-bold);
  color: #1e293b;
}

.MuiChip-root {
  font-weight: var(--font-weight-regular);
  border-radius: var(--border-radius);
}

/* New Styles for Saldo Display */
.saldo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 25px;
}

.saldo-icon {
  margin-right: 8px;
}

.saldo-text {
  font-size: 1.25rem;
  font-weight: 500;
}

/* Widget Closed State - Bubble in Bottom Right */
.widget-open-bubble {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 60px;
    height: 60px;
    background-color: var(--color-primary);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.widget-open-bubble:hover {
    background-color: #3751a0; /* Darker shade of primary color */
    transform: scale(1.05);
}